import React, { Component, PropTypes } from 'react';
import "../stylesheets/Projects.css";
import project1img from "../images/Not-available.png";
import image from "../images/preview.jpg";
import Project from "./Project.js";

/**
 * Projects
 */
export class Projects extends Component {


  render() {
    return (
      <div className="section row justify-content-center">
        <div id="projectSection" className="col-lg-12 col-xs-12">
          <div className="text-center text-dark">
              <h3 className="display-4 lined"><span className="title"> Projects </span></h3>
            <h3 className="lead">Note: <small> Most of my recent projects at my current job are unavailable to demo. But I am working to re-creating these projects soon! </small></h3>
          </div>
          <div className = "card-scroll-container d-flex flex-row flex-nowrap">
            <Project
              image={project1img}
              title={"WF Demo Apps"}
              description={"Using React, Express and NodeJS, created a standardized template to send Wells Fargo API requests and read their responses in a Bootstrapped UI."}
              link={"/"}
              source={"/"}
              badges={["NodeJS", "React"]}
            />
            <Project
              image={project1img}
              title={"Snippet Generator"}
              description={"Using React, NodeJS and the FileSystem module, created a UI to convert sample cURL requests into pre-formatted snippet files in Node, Ruby, PHP, Python, and Java"}
              link={"/"}
              source={"/"}
              badges={["NodeJS", "React"]}
            />
            {/* <Project
              image={image}
              title={"Project 3"}
              description={" This is a longer card with supporting text below as a natural lead-in to additional content. This content is a little bit longer."}
              link={"/"}
              source={"/"}
              badges={["Ruby", "React"]}
            /> */}
            </div>
          </div>
        </div>
    );
  }
}

// Projects.propTypes = {
//   prop: PropTypes.type.isRequired
// }

export default Projects;
